@import './asset/font/font.css';
html{
  font-family: "Roboto", "NotoSansKR";
}

*{
  margin: 0; padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: #fff;
}
ul,ol{
  list-style: none;
}
.scroll::-webkit-scrollbar {
  display: none;
}
.scroll {
  overflow: hidden;
}
.App::-webkit-scrollbar {
  display: none; 
}
.threeModel{
  position: absolute;
  background: #000016;
  top:0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 1;
}
canvas{
  position: relative;
  width: 100%;
  height: 100vh ;
  z-index: -90;
}

